@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
.cart {
  display: block;
  width: 105px;
  height: 100%;
}

.cart-link {
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: rgb(163, 163, 163);
}

.cart-dropdown {
  position: absolute;
  background-color: black;
  color: white;
  top: 100%;
  width: auto;
  overflow: hidden;
  max-height: 0;
  transition: all 0.6s;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.25);
}

.cart-dropdown--active {
  max-height: 600px;
}

.cart-dropdown ul {
  list-style-type: none;
}

.cart-dropdown ul li {
  padding: 20px 30px;
  border: 1px solid rgba(163, 163, 163, 0.5);
  cursor: pointer;
}

.cart-dropdown ul li:hover {
  background-color: #4747ff;
}

.navbar {
  width: 100%;
  height: 50px;
  background-color: #343a40;
  margin-bottom: 20px;
  box-shadow: 0 1px 10px #343a40;
  position: sticky;
  top: 0;
  z-index: 1;
}

.navbar__container {
  width: 82%;
  height: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar span {
  color: #fff;
  display: block;
  font-size: 1.5rem;
}

.product {
  overflow: hidden;
  background-color: white;
  border: 1px dashed black;
  width: 100%;
  height: 100%;
  transition: all 0.2s;
}

.product:hover {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.25);
}

.product-img-wrapper {
  position: relative;
  margin: auto;
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-img-wrapper::after {
  content: '';
  display: block;
  border-bottom: 1px dashed black;
  position: absolute;
  width: 100%;
  top: 340px;
}

.product img {
  width: 80%;
  height: 90%;
}

.product-info {
  width: 100%;
  height: 40%;
  padding: 15px;
}

.product-title {
  font-size: 0.95rem;
  text-decoration: none;
}

.product-title:hover {
  text-decoration: underline;
}

.product-price {
  display: block;
  font-size: 1.4rem;
  font-weight: 500;
  margin: 15px 0;
}

.product-description {
  display: block;
  font-size: 0.85rem;
}

.add-btn {
  display: block;
  width: 50%;
  height: 0;
  background-color: #4747ff;
  color: white;
  margin: auto;
  margin-top: 15px;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
}

.add-btn--active {
  height: 40px;
}

.products {
  width: 100%;
  position: relative;
  margin-bottom: 50px;
}

.products-grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 285px);
  grid-auto-rows: 570px;
  justify-content: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.pagination {
  margin-top: 40px;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.pagination-item {
  width: 60px;
  background-color: aliceblue;
  color: black;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.125);
  cursor: pointer;
}

.sidebar-box {
  background-color: aliceblue;
  border: 1px solid rgba(0, 0, 0, 0.125);
}

.shop-wrapper {
  width: 70%;
  height: auto;
  margin: auto;
  display: grid;
  grid-template-columns: 260px 1fr;
}

.shop-sidebar {
  background-color: white;
}
