.shop-sidebar ul {
  list-style: none;
}

.shop-sidebar li {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  display: flex;
  justify-content: left;
  align-items: center;
  padding-left: 20px;
}

.shop-sidebar li label {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.shop-sidebar li label input[type='checkbox'] {
  width: 20px;
  height: 20px;
  margin-right: 15px;
  cursor: pointer;
}

.counter {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 42.5%;
}

.counter input {
  outline: none;
  border: none;
  background-color: #d3d3d3;
  width: 65%;
  height: 40px;
  text-align: center;
}

.counter-buttons-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 15%;
  min-width: 16px;
}

.counter-btn {
  width: 100%;
  height: 50%;
  cursor: pointer;
  outline: none;
  border: 1px solid rgba(163, 163, 163, 0.5);
}

.counter-btn:hover {
  background-color: rgba(163, 163, 163, 0.8);
}

.cart-product {
  width: 100%;
  min-height: 160px;
  border: 1px solid rgba(163, 163, 163, 0.5);
  padding: 20px 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  overflow: hidden;
}

.cart-product img {
  display: block;
  height: 100%;
  width: 10%;
}

.cart-product-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  padding-left: 20px;
  width: 65%;
  font-size: 1rem;
}

.cart-product-info span {
  display: block;
  font-weight: 600;
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.cart-product-controller {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: center;
  width: 20%;
  margin-left: 20px;
}

.cart-product-controller span {
  display: block;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 20%;
}

.remove-product-from-cart-btn {
  cursor: pointer;
  width: 15%;
  background-color: white;
  border: 1px solid #d21313;
  color: #d21313;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  transition: all 0.2s;
}

.remove-product-from-cart-btn:hover {
  background-color: #d21313;
  color: white;
}

@media only screen and (max-width: 1000px) {
  .cart-product-info span {
    font-size: 1.1rem;
  }
  .cart-product-info {
    font-size: 0.9rem;
  }
}

@media only screen and (max-width: 760px) {
  .cart-product {
    flex-direction: column;
  }
  .cart-product img {
    width: 60%;
    margin-bottom: 40px;
  }
  .cart-product-info {
    width: 100%;
    padding: 0;
    margin-bottom: 30px;
    text-align: center;
  }
  .cart-product-controller {
    width: 80%;
    margin-left: 0;
  }
}

.shipping-cart {
  width: 80%;
  max-width: 1080px;
  border: 1px solid rgba(163, 163, 163, 0.5);
  margin: auto;
  margin-top: 80px;
  margin-bottom: 80px;
  height: auto;
  border-radius: 5px;
  overflow: hidden;
}

.shipping-cart-header {
  width: 100%;
  background-color: #343a40;
  padding: 10px 20px;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.shipping-cart-header i {
  margin-right: 10px;
}

.shipping-cart-footer {
  width: 100%;
  background-color: #343a40;
  padding: 20px 20px;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.shipping-cart-footer span {
  font-weight: 400;
  margin: 0 10px;
  font-size: 2rem;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  line-height: 1.15;
}

